.week-day-container {
  display: flex;
  flex-wrap: wrap; }

.week-day {
  width: 110px;
  height: 140px;
  padding: 10px;
  text-align: center;
  border-radius: 10px;
  margin: 15px;
  background-color: #ffffff;

  .weather-icon {
    width: 60px;
    margin: auto;

    svg {
      width: 100%;
      height: 50px; } }

  .max-temperature {
    font-weight: 700;

    &::after {
      content: '\00B0'; } } }
