.wind-component {
  width: 200px;
  height: 150px;
  padding: 10px;
  text-align: center;
  border-radius: 10px;
  margin: 15px;
  background-color: #ffffff;

  .wind-speed {
    font-size: 2rem; }

  .wind-direction {
    margin-top: 15px;
    svg {
      width: 20px; } }

  .wind-component-title {
    span {
      color: #BFBFBF; } }

  span {
    display: block; } }
