.current-weather-component {
  background-color: #ffffff;
  border-bottom-left-radius: 50px;
  border-top-left-radius: 50px;

  .current-weather-icon {
    padding: 15px;
    svg {
      display: block;
      width: 270px;
      max-width: 100%;
      max-height: 200px; } }


  .current-date-time {
    font-size: 2rem; }

  .current-weather-temperature {
    font-size: 4rem;

    &::after {
      content: '\00B0'; } }

  .current-weather-details {
    padding: 0 20px 20px 20px; } }
